* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color:oklch(26.8% 0.007 34.298) ;
    font-family: "Sono",sans-serif;
    color:oklch(98.5% 0.001 106.423);
    padding: 48px 64px 0px;
}

.hidden {
    display: none !important;
}

.message {
    text-align: center;
    text-transform: uppercase;
    font-size: 32px;
    font-weight: 600;
    margin-top: 24px;
}

.disputed {
    color: #ef4444;
    font-weight: 600;
    margin-right: 10px;
}

/* MAIN LAYOUT*/
#root {
    max-width: 16000px;
    margin: 0 auto;
    
}

.header {
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}



.main{
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 48px;

  height: calc(100vh - 48px - 68px - 40px);
}

@media (max-width: 900px) {
    .main {
        grid-template-columns: 1fr;
    }
}

section {
    overflow: scroll;
    padding-bottom: 40px;
}

/*ELEMENTS*/
.logo {
    display: flex;
    align-items: center;
    gap: 16px;
  } 
.logo img {
    width: 68px;
    height: 68px;
}

h1 {
    font-size : 42px;
    text-transform: uppercase;
    font-family: "Coiny",sans-serif;
    line-height: 1;
    margin-top: 6px;
}

.category {
    margin-bottom: 16px;
}

.fact {
    font-size: 20px;
    line-height: 1.4;
    background-color:oklch(37.4% 0.01 67.558);
    margin-bottom: 16px;
    padding: 16px 24px;
    letter-spacing: -1px;
    border-radius: 16px;

    display: flex;
    align-items: center;
    gap: 24px;
}

@media (max-width:1200px){
    .fact {
        flex-direction: column;
        align-items: flex-end;
        gap: 12px;
    }
}
.tag{
    text-transform: capitalize;
    font-size: 14px;
    font-family: "Coiny",sans-serif;
    padding-top: 3px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 100px;
    
}

.vote-buttons {
    margin-left: auto;
    flex-shrink: 0;

    display: flex;
    gap: 8px;
}

.vote-buttons button {
    border: none;
    background-color: oklch(44.4% 0.011 73.639);
    font-size: 18px;
    padding: 6px 12px;
    border-radius: 100px;
    color: inherit;
    font-family: inherit;
    font-weight: 600;
    cursor: pointer;
    transition: 0.3s;
}
.vote-buttons button:hover{
     background-color:oklch(21.6% 0.006 56.043) ;
}

.vote-buttons button:disabled{
    background-color:oklch(44.4% 0.011 73.639) ;
}


ul {
    list-style: none;
}


.source:link, .source:visited {
    color: oklch(55.3% 0.013 58.071) ;
    text-decoration: none;
    margin-left: 12px;
    transition: 0.3s;
}

.source:hover, 
.source:active {
    color: #3b82f6;
}

.btn {
    border: none;
    font-family:'Coiny', sans-serif ;
    line-height: 1;
    text-transform: uppercase;
    font-size: 17px;
    padding: 16px 0 13px;
    background-image: linear-gradient(135deg, #3b82f6,#16a34a,#ef4444,#eab308);
    color: inherit;
    border-radius: 100px;
    cursor: pointer;
    transition: 0.3s;
}

.btn:hover {
   transform: scale(110%) rotate(-2deg); 
}

.btn-large{
    font-size: 20px;
    padding: 20px 32px 17px;
}

.btn-all-categories {
   margin-bottom: 16px;
   width: 100%;
}

.btn-category {
    width: 100%;
    background-image: none;
}

/* FORM */

.fact-form {
    background-color:oklch(37.4% 0.01 67.558) ;
    margin-bottom: 40px;
    padding: 16px 32px;
    display: flex;
    align-items: center;
    gap: 16px;
    border-radius: 16px;
}


.fact-form input, 
.fact-form select {
    width: 220px;
    background-color: oklch(44.4% 0.011 73.639);
    border: none;
    border-radius: 100px;
    padding: 16px;
    font-size: 18px;
    color: inherit;
    font-family: inherit;
}

.fact-form input::placeholder {
    color:oklch(70.9% 0.01 56.259) ;
}

.fact-form input:first-child{
    flex-grow: 1;
}

.fact-form span {
    font-weight: 600;
    font-size: 18px;
    margin-right: 18px;
}
@media(max-width:1000px) {
    body {
        padding: 32px 48px;
    }
    .fact-form {
        flex-direction: column;
        align-items: stretch;
    }
    .fact-form input, .fact-form select {
        width: auto;
    }
}

@media (max-width: 900px) {
    .main {
      grid-template-columns: 1fr;
      height: auto;
    }
  }
   
  @media (max-width: 600px) {
    #root {
      padding: 24px;
    }
   
    .header {
      flex-direction: column;
      gap: 16px;
    }
   
    .fact-form {
      padding: 16px;
    }
   
    .fact {
      padding: 12px 16px;
    }
  }



  /* Chrome, Edge, Safari */
::-webkit-scrollbar {
    width: 8px;
  }
  
  ::-webkit-scrollbar-track {
    background: #1e1e1e; /* Matches your background */
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: #3a3a3a; /* Dark gray thumb */
    border-radius: 10px;
    border: 2px solid #1e1e1e; /* Matches background for spacing effect */
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background-color: #555; /* Slightly lighter on hover */
  }
  
  /* Firefox */
  * {
    scrollbar-width: thin;
    scrollbar-color: #3a3a3a #1e1e1e;
  }
  